@import url("https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Marck+Script&family=Shadows+Into+Light");

html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
footer,
header,
nav,
section {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
figcaption,
figure,
main {
  display: block;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
dfn {
  font-style: italic;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
details,
menu {
  display: block;
}
summary {
  display: list-item;
}
canvas {
  display: inline-block;
}
template {
  display: none;
}
[hidden] {
  display: none;
}

html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* General Styles */
html {
  font-size: 62.5%;
}

.wrapper {
  max-width: 1400px;
  width: 85%;
  margin: 0 auto;
}

.hidden {
  display: none;
}

h1 {
  font-size: 4rem;
  margin: 0px;
  font-family: "Berkshire Swash", cursive;
  color: #352208;
  text-decoration: underline;
}

h1::first-letter {
  font-family: "Marck Script", cursive;
  font-size: 9rem;
  color: #540804;
}

h2 {
  font-size: 3rem;
  font-family: "Shadows Into Light", cursive;
  margin-bottom: 0px;
}

h3 {
  font-size: 2.5rem;
  max-width: 90%;
  margin: 0 auto;
  font-family: "Shadows Into Light", cursive;
  color: #352208;
  text-transform: capitalize;
}

h4 {
  font-size: 2rem;
  margin: 5px;
  font-family: "Berkshire Swash", cursive;
}

button {
  color: #352208;
  padding: 10px;
  max-width: 175px;
  font-size: 2rem;
  background-color: transparent;
  border: none;
  font-family: "Shadows Into Light", cursive;
  transition: all 0.5s;
}

button::first-letter {
  font-family: "Marck Script", cursive;
  font-size: 4rem;
  color: #540804;
}

button:disabled {
  display: none;
}

button:hover,
button:focus {
  color: #540804;
  font-size: 2.5rem;
}

.App {
  text-align: center;
  background: url("./assets/spellbook.png");
  background-size: cover;
  background-position: 0px -20px;
  min-height: 100vh;
}

/* Main Section Styles */

.flexContainer {
  display: flex;
  justify-content: space-between;
}

.instructions {
  order: 2;
  margin-top: 70px;
}

.options {
  display: flex;
  order: 1;
  margin-top: 50px;
}

.options,
.instructions {
  width: calc(50% - 50px);
}

.classButtons,
.spellLevel {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: relative;
  top: 50px;
}

.classButtons {
  left: 50px;
}

.classButtons button {
  font-size: 4rem;
}

.classButtons button::first-letter {
  font-size: 6rem;
}

.classButtons button:hover,
.classButtons button:focus {
  font-size: 5rem;
}

.spellLevel button:hover,
.spellLevel button:focus {
  border: 2px solid #540804;
}

.spellLevel button {
  transition: all 0.5s;
  margin: 0px 20px;
  border-right: 2px solid #540804;
}

.spellLevel {
  width: 95%;
}

.spells {
  display: flex;
  flex-direction: column;
  max-height: 78vh;
  width: 95%;
  flex-wrap: wrap;
  overflow: scroll;
  position: relative;
  top: 50px;
}

/* spell box styles */

.spellBox {
  max-height: 30vh;
  position: relative;
}

.spellBox p:first-of-type {
  font-size: 2rem;
  font-family: "Berkshire Swash", cursive;
  border: none;
}

.spellBox p {
  font-size: 2rem;
  overflow: auto;
  max-height: 30vh;
  font-family: "Shadows Into Light", cursive;
  border: 2px solid black;
  padding: 5px;
}

.spellBox p:last-of-type {
  max-height: 90px;
  border: none;
}

.reset {
  background: #540804;
  font-family: "Shadows Into Light", cursive;
  font-size: 3rem;
  color: white;
  max-width: 300px;
  transition: all 0.5s;
  border-radius: 0px;
  border: 2px solid white;
  position: absolute;
  bottom: 40px;
  right: 19%;
}

.reset::first-letter {
  font-family: "Shadows Into Light", cursive;
  color: white;
}

.reset:hover {
  color: white;
  font-size: 4rem;
}

footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  text-align: center;
  background-color: #352208;
  font-size: 2rem;
  color: white;
  font-family: "Shadows Into Light", cursive;
}

footer a {
  color: white;
  text-decoration: none;
}

/* Media Queries */

/* tablet landscape */
@media (max-width: 1150px) {
  .App {
    background: url("./assets/8fb07d1d8660048cc089a07047e7e77d.jpg");
    background-size: cover;
    background-repeat: repeat-y;
    background-position: 0px;
  }

  h1 {
    margin-bottom: 40px;
  }

  .flexContainer {
    flex-direction: column;
  }

  .instructions {
    order: 1;
    margin-top: 20px;
  }

  .instructions,
  .options {
    width: 100%;
  }

  .options {
    flex-direction: column;
  }

  .classButtons {
    top: 0px;
    left: 0px;
    flex-direction: row;
    justify-content: space-between;
  }

  .classButtons button {
    margin: 3% 5%;
  }

  .spellLevel {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    border-bottom: 2px solid #540804;
    top: 0px;
  }

  .spellLevel button {
    border-right: none;
  }

  .spells {
    top: 0px;
    max-height: 90vh;
    padding-bottom: 50px;
    align-items: center;
  }

  .spellBox {
    max-height: 100vh;
  }

  .reset {
    position: relative;
    bottom: -40px;
    right: 0px;
  }

  button {
    color: white;
  }

  button:hover,
  button:focus {
    font-size: 2rem;
    color: red;
  }
}

/* tablet portrait and phone landscape */

@media (max-width: 650px) {
  .spells {
    max-height: 110vh;
  }
}

/* phone portrait */

@media (max-width: 425px) {
  .spells {
    max-height: 300vh;
  }
}
